.header-container {
  border-bottom: 1px solid var(--header-white-color);
  background: var(--brown-gradient);
  padding: 14px 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.name-logo {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}
.igor {
  color: var(--header-white-color);
  font-family: Merriweather;
  font-size: 18px;
  line-height: 1.4;
  letter-spacing: 0.36px;
  text-transform: uppercase;
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 4px;
}

.line-left {
  width: 37px;
  height: 1px;
}
.line-right {
  width: 45px;
  height: 1px;
}
.label {
  color: var(--header-white-color);
  font-family: Roboto;
  font-size: 12px;
  line-height: 1.4;
  letter-spacing: 2.88px;
}
.scale-icon {
  fill: var(--header-white-color);
}

.mobile-menu-open {
  background-color: transparent;
  border: none;
  padding: 0;
  line-height: 0;
}

.burger-menu svg {
  fill: var(--header-white-color);
  width: 24px;
  height: 24px;
}
@media screen and (min-width: 1280px) {
  .mobile-menu {
    display: none;
  }
}
/* Кнопка бургер-меню */
@media screen and (max-width: 1279px) {
  .mobile-menu {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1000;
    top: 0;
    left: 0;
    padding: 71px 36px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0.85) 100%), url(../images/mob-menu-w320.webp) lightgray 50% / cover no-repeat;
      
    opacity: 0;
    visibility: hidden;
    // вкл-выкл меню
    pointer-events: none;
    transition: opasity 500ms linear;
    visibility: 500ms linear;
    
  }
  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    max-height: 900px;
  }
  
  .mobile-menu_close-btn {
    position: absolute;
    top: 22px;
    right: 15px;
    background-color: transparent;
    border: none;
    padding: 0;
  }

  .list-mobile_nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .mobile-menu_link {
    color: var(--header-white-color);
    font-family: 'Roboto';
    line-height: 1.4;
  }
  .order-btn {
    padding: 14px 30px;
    background-color: transparent;
    border-radius: 8px;
    border: 1px solid var(--Button, #faac39);
    color: var(--header-white-color);
  }
}
