}

@media screen and (width <= 1279px) {
  .mobile-menu {
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    visibility: .5s linear;
    background: linear-gradient(0deg, #000000d9 0% 100%), #d3d3d3 url("mob-menu-w320.6ebc2ebb.webp") 50% / cover no-repeat;
    width: 100%;
    height: 100%;
    padding: 71px 36px;
    transition: opasity .5s linear;
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    max-height: 900px;
  }

  .mobile-menu_close-btn {
    background-color: #0000;
    border: none;
    padding: 0;
    position: absolute;
    top: 22px;
    right: 15px;
  }

  .list-mobile_nav {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    margin-top: 50px;
    margin-bottom: 50px;
