.product-card:hover .card-image {
  transform: scale(1.15);
}

.caption {
  padding: 6px 2px;
  font-family: 'Merriweather';
  font-size: 11px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.4;
  text-align: center;
  height: 42px;
  align-content: center;
  background-color: #d7d7d7;
}

// feedback-section
.feedback-container {
  padding: 40px 16px;
  background: var(--feedback-background-color);
}
.feedback-container h2 {
  color: #000;
  font-family: Merriweather;
  font-size: 16px;
  line-height: 1.4;
  margin-bottom: 24px;
}
.feedback-container img {
  border-radius: 4px;
  width: 100%;
}

// slider
.slider-slide {
  padding: 10px;
  width: 250px;
}
.slider-slide h3 {
  color: #000;
  font-family: 'Merriweather';
  font-size: 11px;
  line-height: 1.4;
}
.stars {
  display: flex;
  justify-content: flex-start;
  gap: 4px;
  margin-top: 4px;
  margin-bottom: 12px;
}
.stars > li > svg {
  width: 10px;
  height: 10px;
}
.quotes {
  display: flex;
  margin-bottom: 4px;
}
.quotes svg {
  width: 5px;
  height: 7px;
}
.slider-text {
  color: #000;
  font-family: 'Roboto';
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4; /* 15.4px */
}
.quotes-bottom {
  justify-content: flex-end;
}

.slider-controls {
  display: flex;
  justify-content: space-between;
}
.prev-slide,
.next-slide {
  border: none;
  background: transparent;
}

.prev-slide svg,
.next-slide svg {
  width: 32px;
  height: 32px;
}
// order-section
.order-section-container {
  padding: 40px 0px 0px 0px;
  background: var(--main-order-gradient);
}
.order-section-container img {
  height: 300px;
  width: 100%;
  margin: 0 auto;
}

//order-form
.order-form {
  display: flex;
  padding: 20px 15px;
  flex-direction: column;

  background: var(--header-white-color);
}
.order-form h2 {
  font-family: 'Merriweather';
  font-size: 18px;
  font-style: normal;
  line-height: 1.4;
  color: var(--black-main-text-color);
  margin-bottom: 8px;
}

.order-form h3 {
  color: var(--black-secondary-text-color);
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4; /* 19.6px */
  margin-bottom: 20px;
}
.form,
.form-label {
  display: flex;
  flex-direction: column;
}

.form-label {
  color: var(--black-main-text-color);
  font-family: 'Roboto';
  font-size: 12px;
  font-weight: 700;
  line-height: 1.4; /* 16.8px */
}

.form-input {
  border-bottom: 1px solid #000;
  padding: 10px 8px;
  border-left: none;
  border-top: none;
  border-right: none;
}
.input-name {
  margin-bottom: 8px;
}

.btn-submit {
  display: flex;
  align-items: center;
  padding: 14px 40px;
  justify-content: center;
  border-radius: 8px;
  border: 3px solid var(--bronze-button);
  margin: 20px 0px 20px 0px;
  color: var(--black-button-color);
  font-family: 'Roboto';
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.4; /* 19.6px */
}

.btn-icon{
  width: 16px;
height: 16px;
margin-left: 10px;
}

//main-socials
.main-socials{
  padding:0px 15px 20px 15px;
  background: var(--header-white-color);
}
.main-social-icons{
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
}
.social-icon-block a{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;

}
.main-social-icon{
  width: 20px;
height: 20px;
}

.social-title{
color:var(--black-secondary-text-color);
font-family: 'Roboto';
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 1.4; /* 16.8px */
}

//adress-section
.adress-section-container{
  padding: 24px 0px 10px 0px;
  background: var(--feedback-background-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.adress-section-container h2{
  color:var(--black-main-text-color);
  text-align: center;
font-family: 'Merriweather';
font-size: 18px;
line-height: 1.4; 
}

.adress,
.tel-number,
.mail{
  display:flex;

}
.adress p,
.tel-number a,
.mail a{
color:var(--black-main-text-color);
font-family: 'Roboto';
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 1.4; /* 16.8px */
}

.adress svg,
.tel-number svg,
.mail svg{
  margin-right:4px;
}

.map{
  width: 100%;
  height: 200px;
  border: none;

}